.profile__title {
  margin: 0;
  padding: 0;
  font-weight: 500;
  font-size: 42px;
  line-height: 1.14;
  max-width: 295px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

@media (max-width: 767px) {
  .profile__title {
    max-width: 189px;
    font-size: 27px;
    line-height: 1.22;
    justify-self: end;
  }
}
