.card__like-button {
  grid-column: 2;
  grid-row: 2;
  justify-self: end;
  margin: -3px 20px 0 0;
  padding: 0;
  width: 22px;
  height: 19px;
  background-image: url('../../../images/card-like-button-icon.svg');
  background-repeat: no-repeat;
  background-position: center;
  background-color: transparent;
  border: 0;
  align-self: center;
  cursor: pointer;
  opacity: 1;
  transition: opacity .7s ease;
  filter: drop-shadow(0 0 0.2rem rgba(0, 0, 0, 0.2));
}

.card__like-button:hover {
  opacity: 0.5;
}

.card__like-button:focus {
  outline: 0;
  animation: like 0.3s 1;
}

@keyframes like {
  0% {
    transform: scale(1);
  }
  90% {
    transform: scale(1.2);
  }
  100% {
    transform: scale(1.1);
  }
}
